body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%; }

.container__grid {
  height: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 2% 1fr 2%;
  grid-template-rows: 50px 75px 50px 1fr 0.5fr;
  grid-template-areas: "title title title" ". add ." ". filter ." ". content ." ". footer ."; }

.container__title {
  grid-area: title;
  display: flex;
  width: 100%; }

.container__add {
  grid-area: add;
  display: flex;
  width: 100%; }

.container__filter {
  grid-area: filter;
  display: flex;
  width: 100%; }

.container__content {
  grid-area: content;
  display: flex;
  flex-direction: column;
  width: 100%; }

.container__footer {
  width: 100%;
  grid-area: footer;
  display: flex;
  flex-direction: column;
  align-self: end;
  grid-row-start: 5;
  grid-row-end: 5; }

.container__content {
  display: flex;
  align-items: center;
  width: 100%; }

.container__todo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: lightgray;
  min-width: 100%;
  height: 50px;
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: #5ca871;
  box-shadow: 2px -2px 0px 0px #01661d; }
  .container__todo p {
    margin: 0;
    padding: 0;
    color: #f8f8f8; }
  .container__todo s {
    color: #c7f861; }

.container__todo:nth-child(even) {
  background-color: #73a782; }

.todo--content {
  margin-left: 10px;
  flex: 100 1;
  overflow: hidden; }
  .todo--content input {
    width: 100%; }

.todo--icons {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-around;
  height: 100%; }
  .todo--icons i {
    margin: 5px; }
  .todo--icons i:nth-child(1) {
    flex: 1 1;
    color: #0e301d; }
  .todo--icons i:nth-child(2) {
    color: #df0000be; }

.container__filter {
  align-items: center;
  justify-content: center; }
  .container__filter select {
    width: 90%; }

.filter__container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  max-width: 365px;
  height: 50px;
  background-color: #4b4e4c;
  box-shadow: 2px -2px 0px 0px #01661d;
  margin-bottom: 5px;
  border-radius: 3px; }

.container__add {
  align-items: center;
  justify-content: center;
  margin-top: 5px; }

.add--todo__container {
  min-width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #80ad98;
  box-shadow: 2px -2px 0px 0px #4d685b;
  border-radius: 3px; }
  .add--todo__container form {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-left: 10px;
    margin-right: 10px; }
    .add--todo__container form select {
      border: none;
      outline: none;
      background-color: #9fb8ac;
      color: #111005;
      box-shadow: 1px -1px 1px 0px #1c581b;
      font-weight: 900; }
    .add--todo__container form label {
      width: 100%;
      flex: 2 1; }
    .add--todo__container form label:nth-child(2) {
      flex: 0 1; }
  .add--todo__container input {
    width: 100%;
    height: 25px;
    border: 0px;
    outline: 0px;
    margin: 0px;
    padding: 0px;
    box-shadow: 1px -1px 1px 0px #1c581b; }

button {
  width: 25%;
  border: none;
  background-color: #30972e;
  box-shadow: 1px -1px 1px 0px #1c581b;
  outline: none;
  color: #edf0e7; }

.container__title {
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  margin-bottom: 15px;
  display: flex; }

.title--image {
  margin: auto;
  margin-left: 5%;
  margin-right: 5%;
  max-width: 365px;
  min-width: 265px; }

.container__footer {
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 10px; }

.footer--content {
  height: 1009%;
  margin-top: 5px;
  margin-bottom: 10px;
  flex: 1 1;
  width: 265px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 2px -2px 2px 0px #1c581b75;
  background-color: #d0e2d6; }
  .footer--content p {
    margin: 5px;
    margin-bottom: 0px;
    padding: 5px;
    color: #1c581b;
    text-shadow: 1px 1px #edf0e7; }

html {
  padding: 0;
  margin: 0; }

body {
  padding: 0;
  margin: 0;
  background-color: #c4d8cb; }

p,
s {
  margin: 0px;
  padding: 0px;
  font-family: "Ubuntu", sans-serif;
  font-size: 20px; }

button {
  font-family: "Press Start 2P", cursive;
  font-size: 10px;
  font-weight: 300; }

option {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300; }

